.trust-card{
  position: relative;
  padding-bottom: 100%;
  filter: grayscale(1);
  border-radius: 50%;
  img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    max-width: 100%;
    max-height: 100%;
  }
  &:hover{
    filter: grayscale(0);
    background: #D4EBF1;
  }
}

.category-card{
  background: #D4EBF1;
  padding: 9px 37px 28px;
  position: relative;
  &.color2{
    background: $blockBg;
    .category-card__link{
      background: $blockBg;
      path{
        stroke: $black;
      }
      &:hover{
        background: rgba($blockBg,20%);
        opacity: 1;
      }
    }
  }
  &__img{
    position: relative;
    padding-bottom: 65%;
    display: block;
    margin-bottom: 9px;
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 25px;
  }
  &__link{
    width: 80px;
    height: 80px;
    border: 10px solid $pageBg;
    border-radius: 50%;
    position: absolute;
    bottom: -20px;
    right: -20px;
  }
}

.use-card{
  background-color: #fff;
  padding: 41px 20px 35px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__img{
    height: 151px;
    display: flex;
    align-items: center;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
  &__title{
    margin-top: 17px;
  }
  &__num{
    font-weight: 500;
    font-size: 245px;
    font-family: $font2;
    opacity: 0.07;
    font-variant-numeric: tabular-nums;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
  }
}

.material-card{
  display: flex;
  flex-direction: column;
  &__num{
    color: $primary;
    text-align: right;
  }
  &__title{
    margin: 17px 0 35px;
  }
  &__img{
    margin-top: auto;
    position: relative;
    padding-bottom: 89.2%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.news-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:not(:last-child){
    padding-bottom: 35px;
    margin-bottom: 35px;
    border-bottom: 1px solid $blockBg;
  }
  &__img{
    flex-shrink: 0;
    width: 23.4%;
    display: block;
    img{
      width: 100%;
    }
  }
  &__content{
    flex-grow: 1;
    max-width: 760px;
    width: 100%;
  }
  &__day,
  &__month{
    letter-spacing: 0.05em;
  }
  &__month{
    font-family: $font2;
    font-size: 20px;
    line-height: 23px;
    margin-top: 7px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: 9px;
  }
}
