.main{
  padding: 207px 0 80px;
  background-image: url("../img/main-bg.svg"), linear-gradient(180deg, #F4F5F9 19.27%, #E3E5EF 102.72%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: -118px;
  &__inner{
    display: grid;
    grid-template-columns: 795px calc(100% - 825px);
    gap: 30px;
  }
  &__desc{
    margin-top: 43px;
    line-height: 150%;
    max-width: 490px;
  }
  &__buttons{
    margin-top: 49px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 45px;
    &:not(:last-child){
      margin-right: 16px;
    }
    svg{
      margin-left: 20px;
    }
  }

  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin:0 0 0 10px;
      }
    }
    &-pagination{
      position: static;
      margin: 0 15px;
      width: 132px;
      height: 3px;
      background: #D7DAE6;
      border-radius: 4px;
      .swiper-pagination-progressbar-fill{
        background-color: $primary !important;
      }
    }
  }
  &__slider{
    &-bottom{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 63px;
    }
    &-pagination{
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 16px;
    }
    &-nav{
      display: flex;
      align-items: center;
      margin-left: 48px;
    }
  }
}

.trust{
  padding: 63px 0 88px;
  &__slider{
    margin-top: 83px;
    display: grid;
    grid-template-columns: 60px calc(100% - 200px) 60px;
    gap: 40px;
    align-items: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-slide{


    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin: 0;
      }
    }
  }
}

.block{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.categories{
  padding: 85px 0 100px;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - ((46px * 3) / 4)));
    gap: 35px 46px;
  }
}

.use{
  padding: 100px 0 116px;
  background: $blockBg;
  margin-top: 23px;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(3, calc((100% / 3) - ((46px * 2) / 3)));
    gap: 35px 46px;
  }
}

.consult{
  padding: 150px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 48.75%;
    gap: 30px;
  }
  &__content{
    max-width: 608px;
    display: flex;
    flex-direction: column;
  }
  &__desc{
    padding-top: 40px;
    max-width: 400px;
    margin-top: auto;
    &--mt40{
      margin-top: 0;
    }
  }
  &__form{
    &-grid{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  }
  &__full{
    grid-column: 1/3;
  }
  &__btn{
    margin: 61px 0 0 auto;
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
  &__contacts{
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    &-title{
      margin-bottom: 13px;
    }
  }
}

.materials{
  padding: 25px 0 140px;
  &__desc{
    margin-top: 26px;
  }
  &__grid{
    margin-top: 63px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - ((38px * 3) / 4)));
    gap: 35px 38px;
  }
}

.service{
  padding: 100px 0 55px;
  background-color: $blockBg;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    width: 100%;
  }
  &__grid{
    width: 100%;
    margin-top: 70px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - ((38px * 3) / 4)));
    gap: 35px 38px;
  }
  &__icon{
    height: 60px;
    margin-bottom: 25px;
    img{
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 25px;
  }
  &__link{
    margin-top: 65px;
    padding: 0 46px;
    svg{
      margin-left: 25px;
    }
  }
}

.sections-group{
  position: relative;
  &__img{
    position: absolute;
    right: 0;
    bottom: -30px;
  }
  section{
    position: relative;
    z-index: 2;
  }
}

.news{
  padding: 128px 0 43px;
  &__grid{
    margin-top: 58px;
  }
}

.contacts{
  margin-bottom: 50px;
  padding: 42px 0;
  position: relative;
  z-index: 2;
}

.map{
  height: 618px;
  width: 100%;
}

.contact-card{
  position: absolute;
  background: #FFFFFF;
  right: calc(50% - 775px);
  top: 50%;
  transform: translateY(-50%);
  width: 526px;
  min-height: 100%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  &__tabs{
    display: flex;
    align-items: center;
  }
  .tab{
    flex-grow: 1;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $blockBg;
    font-weight: 600;
    font-size: 18px;
    font-family: $font2;
    color: $black;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      background-color: rgba($blockBg,.8);
    }
    &--active{
      pointer-events: none;
      background-color: transparent;
      color: $primary;
    }
  }
  &__content{
    padding: 47px 50px 57px;
    flex-direction: column;
    flex-grow: 1;
    &.tab__content--active{
      display: flex;
    }
  }
  &__items{
    margin-top: 40px;
  }
  &__item{
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
  &__desc{
    margin-top: 13px;
  }
  &__nav{
    margin-top: auto;
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 26px 0 31px;
    svg{
      margin-left: 11px;
    }
  }
}
