.footer{
  padding: 86px 0 47px;
  background-color: #fff;
  &__col{
    max-width: 358px;
    &:not(:last-child){
      margin-right: 50px;
    }
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 39px;
    }
  }
  &__title{
    margin-bottom: 32px;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    color: $black;
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 13px;
    }
  }
  &__form{
    width: 356px;
    position: relative;
    margin-top: 15px;
    input{
      padding-right: 55px;
    }
  }
  &__btn{
    position: absolute;
    bottom: 10px;
    right: 0;
    width: 52px;
    height: 52px;
    border-radius: 50%;
  }
  &__contacts{
    &-item{
      display: flex;
      align-items: stretch;
      &:not(:last-child){
        margin-bottom: 21px;
      }
    }
    &-icon{
      margin-right: 19px !important;
      flex-shrink: 0;
      min-height: 52px;
      height: unset;
      border-radius: 90px;
      pointer-events: none;
    }
    &-desc{
      padding-top: 5px;
    }
  }
  &__right{
    width: 356px;
    flex-shrink: 0;
  }
  &__conf{
    text-decoration: underline;
  }
  &__c,
  &__conf{
    color: $black;
  }
  &__logo{
    margin-bottom: 27px;
  }
  &__logo2 {
    width: 117px;
    display: block;
    margin-top: 10px;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover svg>g {
      opacity: .6
    }
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__contacts2{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__phone,
  &__mail{
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 17px;
    }
  }
}

.footer-top{
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-bottom{
  padding-top: 42px;
  margin-top: 54px;
  border-top: 1px solid rgba(102, 105, 115, 0.27);
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
