@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
.fg {
    position: relative;
    margin: 0;
    input,
    textarea{
        border: none;
        border-bottom: 1px solid rgba(102, 105, 115, 0.2);
        padding: 0;
        height: 60px;
        width: 100%;
        font-size: 24px;
        font-family: $font2;
        background-color: transparent;
        color: $black;
    }
    textarea{
        resize: none;
    }
    label{
        display: block;
        margin-bottom: 3px;
        font-size: 18px;
        line-height: 133%;
        color: $gray;
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                // background-color: $green;
                border: 3px solid #fff;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 40px;
        position: relative;
        min-height: 20px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 2px;
            background: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.45);
            width: 20px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: all .100s;
        }
    }
}

.tab{
    &__content{
        display: none;
        &--active{
            display: block;
        }
    }
}
