@import "../mixins";
.logo{
  display: block;
  width: 169px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 25px 0;
  position: relative;
  z-index: 2;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    margin-right: 44px;
  }
  &__mob{
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__contacts{
    text-align: right;
    margin-left: auto;
    padding-left: 20px;
    flex-shrink: 0;
  }
  &__phone{
    font-weight: 700;
    font-size: 22px;
    line-height: 150%;
    font-family: $font2;
    color: $primary;
    display: block;
    &:hover{
      color: $black;
    }
  }
  &__mail{
    font-weight: 700;
    font-size: 18px;
  }
  &__search{
    margin-left: 30px;
    width: 191px;
    input{
      width: 100%;
      height: 52px;
      padding: 0 10px 0 57px;
      background-color: $bg;
      border-radius: 1000px;
      border: none;
      background-image: url(../img/search.svg);
      background-repeat: no-repeat;
      background-position: left 20px center;

    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-left: 10px;
  }
  &__social,
  &__lang{
    font-weight: 700;
    font-size: 18px;
    font-family: $font2;
    color: $primary;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__item{
    text-align: center;
    font-size: 18px;
    line-height: 150%;
    position: relative;
    &:not(:last-child){
      margin-right: 24px;
    }

    &-show{
      display: flex;
      align-items: center;
      line-height: inherit;
      transition: all .3s;
      svg{
        margin-left: 13px;
      }
      &:hover{
        color: $primary;
        cursor: pointer;
        path{
          stroke: $primary;
        }
      }
    }
    &-hidden{
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      padding-top: 44px;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-inner{
        filter: drop-shadow(0px 4px 36px rgba(0, 0, 0, 0.1));
        background: #F7F8FA;
      }
    }
    &-link{
      min-width: 172px;
      padding: 0 30px;
      height: 57px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        border-bottom: 1px solid #E3E5EF;
      }
    }
    &--active{
      .nav__item-show{
        color: $primary;
        path{
          stroke: $primary;
        }
        svg{
          transform: rotate(180deg);
        }
      }
      .nav__item-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &--catalog{
      .nav__item-hidden{
        position: fixed;
        left: 0;
        width: 100vw;
        top: 40px;
        height: calc(100vh - 40px);
        transform: unset;
        padding-top: 78px;
      }
    }
  }
}

.header-catalog{
  background-color: #fff;
  z-index: 10;
  height: 100%;
  width: 100%;
  text-align: left;
  padding: 30px 0 50px;
  &__scroll{
    width: calc(100% - 24px);
    margin: 0 auto;
    height: 100%;
    overflow-y: auto;
    @include scroll;
  }
  .container{
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__inner{
    flex-grow: 1;
    column-count: 4;
    column-gap: 45px;
  }
  &__group{
    display: inline-block;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  &__title,
  &__link{
    font-size: 18px;
    line-height: 150%;
  }
  &__title{
    font-weight: 700;
    margin-bottom: 25px;
    svg{
      display: none;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
}
