.display1{
  font-weight: 500;
  font-size: 45px;
  line-height: 131%;
  font-family: $font2;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.display2{
  font-size: 24px;
}

.display3{
  font-size: 19px;
}

.display2,
.display3{
  font-weight: 600;
  line-height: 150%;
  font-family: $font2;
}

.body-text{
  font-size: 18px;
  line-height: 133%;
  color: $gray;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}
